@import url(https://fonts.googleapis.com/css2?family=Limelight&family=Roboto:wght@300;400&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto,
    'Helvetica Neue', Arial, sans-serif, 'Apple Color Emoji', 'Segoe UI Emoji',
    'Segoe UI Symbol';
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  height: 100%;
  background-color: #fff;
  color: #000;
  --font-family-monospace: SFMono-Regular, Menlo, Monaco, Consolas,
    'Liberation Mono', 'Courier New', monospace;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

html,
body {
  width: 100%;
  height: 100%;
  margin: 0px;
  padding: 0px;
  overflow-x: hidden;
}

.App {
  /* font-family: 'Helvetica Neue', Helvetica, Arial, sans-serif; */
  font-size: 18px;
}

.header {
  font-family: 'Limelight', cursive;
  font-size: 25px;
  text-decoration: none;
  color: #fff;
  height: 50px;
}

.header:hover {
  color: #fff;
}

.nav-link {
  margin: 0.5rem 1rem;
}

.nav-link a {
  color: #fff;
  text-decoration: none;
}

.nav-link a:hover {
  color: #fff;
  text-decoration: underline;
}

.custom-button {
  border-radius: 20px !important;
  padding: 2px 15px;
  background-color: #fff;
  color: #353a3d;
  border: 1px solid #353a3d;
  font-size: 16px;
}

.custom-button svg {
  margin-bottom: 4px;
  font-size: 18px;
}
.btn:focus {
  outline: none !important;
  box-shadow: none !important;
}

.popover-button {
  margin-top: 1rem !important;
  text-align: center;
}

.popover-body {
  font-size: 16px;
  cursor: pointer;
  padding: 0 1rem 1rem 1rem !important;
}

.popover-body:hover {
  font-weight: 500;
}

.no-padding-top {
  padding-top: 0 !important;
}

.smaller-font {
  font-size: 16px;
  font-weight: 400;
  width: 400px !important;
}

.not-found-margin-top {
  margin-top: 15rem;
}

.display-margin {
  margin-top: 2rem;
  margin-bottom: 2rem;
  text-align: center;
}

.logout-button {
  font-size: 18px !important;
  text-decoration: none !important;
}

.logout-button:hover {
  text-decoration: underline !important;
}

.no-underline a {
  text-decoration: none;
}

.no-underline a:hover {
  text-decoration: underline;
}

.strike {
  display: block;
  text-align: center;
  overflow: hidden;
  white-space: nowrap;
}

.strike > span {
  position: relative;
  display: inline-block;
  color: #4a5568;
}

.strike > span:before,
.strike > span:after {
  content: '';
  position: absolute;
  top: 50%;
  width: 9999px;
  height: 1px;
  background: #cbd5e0;
}

.strike > span:before {
  right: 100%;
  margin-right: 15px;
}

.strike > span:after {
  left: 100%;
  margin-left: 15px;
}

.google-icon {
  margin-right: 10px;
  margin-bottom: 2px;
}

.muted-link {
  font-size: 16px;
}

.favorites-container {
  margin-top: 2.5%;
  margin-bottom: 10%;
}

.align-left {
  text-align: left;
  font-size: 16px;
}

.favorites-mapping-container {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  grid-gap: 10px;
  gap: 10px;
}

.favorite-card {
  max-height: 150px;
  flex-direction: row !important;
  width: auto !important;
  max-width: 300px;
  cursor: pointer;
  min-height: 148px !important;
}

.card-img {
  max-width: 100px !important;
  width: auto !important;
  height: auto;
}

.favorite-card {
  position: relative !important;
}

.card-title {
  font-size: 18px !important;
  font-weight: 400 !important;
}

.add-to-list {
  left: 110px;
}

.star {
  right: 10px;
}

.remove-movie {
  left: 145px;
}

.floating-buttons {
  width: 100%;
  display: inline;
  overflow: auto;
  white-space: nowrap;
  margin: 25px auto 0 auto;
}

.float-left {
  float: left;
}

.margin-right {
  margin-right: 2%;
}

.float-right {
  float: right;
}

.no-styling {
  margin: 0.375rem 0.75rem;
  padding: 0 0 !important;
  background-color: #fff;
  color: #212529 !important;
  border: 1px solid #fff;
  font-size: 16px;
}

