  background-color: #e62d39;
  border: 1px solid #e62d39;
  color: #fff;
  cursor: pointer;
}

#search-input:focus {
  outline: none;
}

#search-input:active {
  background-color: none;
}

input:-webkit-autofill {
  background-color: white !important;
}

.movie-card .card {
  cursor: pointer;
  position: relative;
}

.card-icons {
  position: absolute;
  font-size: 1.8em;
  bottom: 10px;
  fill: #000;
  padding: 0 0 !important;
  border: 0 !important;
  background-color: transparent;
  line-height: normal;
}

.gold-fill {
  fill: gold;
}

.search-add-to-list {
  left: 10px;
}

.search-star {
  right: 10px;
}

.movies-container {
  position: relative;
}

.pagination {
  position: absolute;
  bottom: -100px;
  right: -50px;
}

.text-align {
  text-align: left;
}

